.work {
  display: flex;
  margin-bottom: 3rem;
  overflow: hidden;

  background-color: $primary-color;
  box-shadow: 0 15px 25px rgba($black-color, .35);
  border-radius: 5px;

  @include mobile {
    flex-direction: column;
  }
}

.work__preview {
  width: 31rem;
  // flex-grow: 1; //сколько свободного пространства в элементе

  img {
    @include img-cover;
  }

  @include mobile {
    width: 100%;
    height: 20rem;
  }
}

.work__content {
  display: flex;
  flex-direction: column;
  max-width: 29rem;
  padding: 2.5rem;
  margin-top: auto;

  @include mobile {
    max-width: none;
    padding: 1.5rem;
  }
}

.work__title {
  margin-bottom: 3rem;

  font-size: 2.4rem;
  font-weight: 500;

  color: $white-color;

  a {
    color: inherit;
  }

  @include mobile {
    margin-bottom: 1rem;
  }
}

.work__descr {
  margin-bottom: 1rem;

  font-size: 1.6rem;
  line-height: 1.6;
  font-weight: 300;

  color: #d2d2d2;

  @include mobile {
    margin-bottom: 1rem;

    font-size: 1.4rem;
  }
}

.work__footer {
  margin-top: auto;

  text-align: right;

  @include mobile {
    text-align: left;
  }
}
