.nav {
  @include tablet {
    display: none;
  }

  &-mobile {
    display: none;
    margin: 3rem 0;
  }

  @include tablet {
    &-mobile {
      display: block;
    }
  }
}

.nav__list {
  display: flex;

  @include tablet {
    flex-direction: column;

    &:hover {
      background: none;
    }
  }
}

.nav__item {
  position: relative;

  &:hover {

    .nav__link {
      background-color: $dodger-color;
    }

    >.subnav {
      display: block;
    }

    //при навидении выходит дочений элемент и можно наводить на ссылки
  }

  @include tablet {
    &:hover {
      .nav__link {
        background: none;
      }
    }
  }

  // отключаем ховер на tablet
}

.nav__link {
  display: block;
  padding: 1.35rem;

  font-size: 1.1rem;
  line-height: 1.2;
  text-transform: uppercase;

  color: $white-color;

  transition: background-color .1s linear;

  &.links_subnav {
    position: relative;
    padding-right: 3.5rem;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      right: 1.35rem;
      transform: translateY(-35%);

      display: block;

      border-style: solid;
      border-width: 6px 5px 0 5px;
      border-color: $white-color transparent transparent transparent;
    }
  }

  @include tablet {
    padding-left: 0;
    padding-right: 0;

    font-size: 1.6rem;
  }
}
