.stories {
  display: grid;
  margin-bottom: 3rem;

  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-column-gap: 15px;
  grid-template-rows: 195px;

  @include mobile {
    grid-template-columns: 140px 140px 140px 140px;
    overflow: auto;

  }
}

.stories__item {
  position: relative;
  overflow: hidden;

  border-radius: 5px;
  box-shadow: 0 3px 15px rgba($black-color, .2);

  cursor: pointer;

  &:hover {
    .stories__preview {
      transform: scale(1.3);
      transition: transform 5s ease-in-out;
    }

    .stories__title,
    .stories__date {
      opacity: 1;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;

    display: block;
    width: 100%;
    height: 100%;

    background: linear-gradient(to bottom, rgba($black-color, .2), rgba($black-color, 0) 50%, rgba($black-color, .2));

  }
}

.stories__preview {
  @include img-cover;

  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  transition: transform 1s ease-in-out;
}

.stories__title,
.stories__date {
  position: absolute;
  left: 0;
  z-index: 3;

  width: 100%;
  padding: 1.4rem;

  font-size: 1.2rem;
  line-height: 1.3rem;
  color: $white-color;
  opacity: .7;

  transition: opacity .3s linear;
}

.stories__title {
  top: 0;
}

.stories__date {
  bottom: 0;

  text-align: right;
}
