.btn {
  display: inline-block;
  vertical-align: top;
  padding: 1.15em 2.1em;

  font-family: inherit;
  font-size: 1.2rem;
  line-height: 1.1;
  font-weight: 500;

  color: $white-color;
  border: none;
  border-radius: 3em;

  transition: background-color .2s linear;


  &-red {
    background-color: $crimson-color;
    box-shadow: 0 7px 17px rgba($crimson-color, .3);

    &:hover,
    :focus {
      background-color: darken($crimson-color, 10%);
    }
  }

  &-blue {
    background-color: $dodger-color;
    box-shadow: 0 7px 17px rgba($dodger-color, .3);

    &:hover,
    :focus {
      background-color: darken($dodger-color, 10%);
    }
  }

  &-rounded {
    border-radius: 0.41em;
  }

  &-sm {
    padding: 0.7em 1.25em;
  }
}
