.post {
  margin-bottom: 3rem;
  padding: 2.5rem;
  overflow: hidden;

  background-color: $primary-color;
  box-shadow: 0 15px 25px rgba($black-color, .35);
  border-radius: 5px;

  @include mobile {
    padding: 1.5rem;
  }
}

.post__header {
  max-height: 40rem;
  margin: 0 0 2.5rem;
  overflow: hidden;

  &--preview {
    margin: -2.5rem -2.5rem 2.5rem;
  }
}

.post__preview {
  @include img-cover;
}

.post__content {
  margin-bottom: 2rem;
}

.post__title {
  margin-top: 1.5rem;

  font-size: 1.8rem;
  font-weight: 500;

  color: $white-color;

  a {
    color: inherit;

    &:hover {
      text-decoration: underline;
    }
  }

  &-open {
    margin-bottom: .75rem;
  }

  @include mobile {
    margin-top: .5rem;

    font-size: 1.4rem;
  }
}

.related__subtitle {
  font-size: 1.6rem;
  font-weight: 500;

  color: $white-color;
}

.post__descr {
  font-size: 1.4rem;

  color: #d2d2d2;
}

.post__text {
  padding-bottom: 1rem;

  font-size: 1.4rem;

  color: #d2d2d2;
  border-bottom: 1px solid #1c1c1c;

  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 2rem;
    font-weight: 700;
  }

  h2 {
    font-size: 1.1em;
  }

  ul,
  ol {
    margin-bottom: 2rem;
    //list-reset
  }

  ol {
    list-style: decimal;
    list-style-position: inside;
  }

  p {
    margin-bottom: 2rem;
  }

  a {
    color: $dodger-color;
  }

  table {
    margin-bottom: 2rem;

    width: 100%;
  }

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}

//оформление для поста

.post__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mobile {
    flex-direction: column;
    align-items: flex-start;
  }
}

.post__data {
  display: flex;

  color: #828282;
  line-height: 1.6rem;

  a {
    color: inherit;

    &:hover {
      text-decoration: underline;
    }
  }

  @include mobile {
    margin-bottom: 1rem;

    font-size: 1.2rem;
  }
}

.post__data-item {

  text-transform: lowercase;

  &:not(:last-child):after {
    content: "";
    position: relative;
    top: -2px;

    display: inline-block;
    width: 6px;
    height: 6px;
    margin: 0 1.5rem;

    border-radius: 50%;
    background-color: #c4c4c4;
  }
}

.post__read {
  font-size: 1.4rem;
  text-transform: lowercase;

  color: $dodger-color;

  &:hover {
    text-decoration: underline;
  }
}

.post__actions {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;

  font-style: 1.2rem;
  text-transform: lowercase;

  color: $white-color;

  a {
    color: inherit;
    opacity: 0.6;

    transition: opacity .2s linear;

    &:hover {
      text-decoration: underline;
      opacity: 1;
    }
  }
}

.post__actions-icon {
  position: relative;
  top: 3px;

  margin-left: 0.5rem;
}
