.search {
  width: 100%;
  max-width: 21rem;
  background-color: $primary-color;
}

.search__input {
  display: block;
  width: 100%;
  padding: 1.2rem 2rem;

  background: transparent;
  border: none;

  font-family: inherit;
  font-size: 1.2rem;
  color: $white-color;

  &::placeholder {
    color: rgba($white-color, .4);
  }

  &:focus,
  :active {
    outline: none;
  }

  @include tablet {
    height: 4rem;
    padding-top: .9rem;
    padding-bottom: .9rem;

    font-size: 1.6rem;
  }
}
