.add-post {
  padding: 1.5rem 2rem;
  margin-bottom: 3rem;

  background-color: $secondary-color;
  box-shadow: 0 15px 25px rgba($black-color, .45);
  border-radius: 5px;

  @include mobile {
    padding: 1.2em;
  }
}

.add-post__form {
  display: flex;
  align-items: flex-end;
}

.add-post__textarea {
  display: block;
  flex-grow: 1;
  height: 34px;
  overflow: hidden;

  font-size: 1.5rem;
  font-family: inherit;

  border: transparent;
  background: transparent;
  resize: none;

  color: $white-color;

  &::placeholder {
    color: #d2d2d2;
  }

  &:focus,
  :active {
    outline: none;
  }
}

.add-post__form-actions {
  display: flex;
  justify-content: flex-end;
  width: 12rem;
  flex-shrink: 0;

  @include mobile {
    width: 9rem;
  }
}

.add-post__file,
.add-post__send {
  display: block;
  width: 4.4rem;
  height: 4.4rem;

  border: none;
  border-radius: 50%;

  transition: background-color .2s linear;

  cursor: pointer;

  @include mobile {
    width: 3.4rem;
    height: 3.4rem;
  }
}

.add-post__file {
  background-color: #ebebeb;
  background-image: url(../img/add-photo.svg);
  @include bg-cover;
  background-size: 25px;

  &:hover {
    background-color: darken(#ebebeb, 10%);
  }

  input {
    display: none;
  }

  @include mobile {
    background-size: 20px;
  }
}

.add-post__send {
  margin-left: 1.5rem;

  font-size: 0;

  color: transparent;
  background-image: url(../img/angle-right.svg);
  @include bg-cover;
  background-size: 20px;
  background-position-x: 14px;
  background-color: $dodger-color;

  &:hover {
    background-color: darken($dodger-color, 10%);
  }

  @include mobile {
    background-size: 15px;
    background-position-x: 11px;
  }
}
