.header {
  position: fixed;
  top: 0;
  right: 0;
  z-index: var(--z-index-header);

  display: flex;
  width: calc(100% - var(--sidebar-width));

  background-color: #0d0d0d;
  box-shadow: 0 5px 22px rgba($black-color, .25);

  @include tablet {
    width: 100%;
  }
}

.header__left {
  width: 60%;

  @include tablet {
    width: 40%;
  }
}

.header__right {
  display: flex;
  justify-content: flex-end;
  width: 40%;

  @include tablet {
    width: 60%;
  }
}
