.form {
  &--authoriz {
    max-width: 2.8rem;
    margin: 0 auto;
  }
}

.form__group {
  position: relative;
  margin-bottom: 1.5rem;

  &-medium {
    margin-bottom: 2.5rem;
  }
}

.form__control {
  margin-bottom: 1.5rem;
  display: block;
  width: 100%;
  padding: 0.5rem 0 .75rem;

  font-family: inherit;
  font-size: 1.4rem;

  color: $white-color;
  background: none;
  border: none;
  border-bottom: 1px solid #3f3f3f;
  border-radius: 0;
  box-shadow: none;

  outline: none;
  // в safary появляется по умолчанию скругления и тень

  &::placeholder {
    color: rgba($white-color, .2);
  }

  &:focus + .form__line {
    width: 100%;
  }

  //form__line следующим элементом

  &--textarea {
    height: 30px;
    overflow: hidden;
    resize: none;
  }
}

.form__line {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;

  width: 0;
  height: 1px;

  background-color: $dodger-color;

  transition: width .2s linear;
}

.form__footer {
  text-align: left;

  &-center {
    text-align: center;
  }
}

.form__footer-list {
  display: flex;
  justify-content: center;

  line-height: 1.2;

  li {
    margin: 0 .5rem;

    text-transform: lowercase;
  }

  a {
    color: $white-color;

    &:hover {
      text-decoration: underline;
    }
  }
}
