.pagination {
  display: flex;
  justify-content: center;
  margin-top: 8rem;

  @include tablet {
    margin-top: 5rem;
  }
}

.pagination__item {
  margin: 0 .5rem;
}

.pagination__link {
  display: block;
  width: 4rem;
  height: 4rem;

  box-shadow: 0 0 10px rgba($black-color, .15);

  background-color: #0d0d0d;
  border-radius: 3px;

  font-size: 1.4rem;
  line-height: 4rem;
  text-align: center;
  
  color: $white-color;

  transition: background-color .2s linear;

  &:hover {
    background-color: lighten(#0d0d0d, 2%);
  }

  &.active {
    background-color: $dodger-color;
    box-shadow: 0 0 10px rgba($dodger-color, .15);
  }
}
