.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-index-sidebar);

  width: var(--sidebar-width);
  height: 100vh;

  background-color: $primary-color;
  box-shadow: 5px 0 15px rgba(#050505, .1);

  @include tablet {
    top: 4rem;

    display: flex;
    flex-direction: column;
    height: calc(100vh - 4rem);
    padding-bottom: 1.5rem;
    overflow: auto; //для скрола sidebar

    transform: translateX(-100%);
    transition: transform .2s linear;

    .show-sidebar & {
      //селектор выше по уровню
      transform: translateX(0);
    }
  }
}

.sidebar__header {
  position: relative;

  height: 18rem;

  background-color: $secondary-color;


  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include tablet {
    height: 14rem;
  }

}

.sidebar__header::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;

  width: 100%;
  height: 65%;

  background: linear-gradient(180deg, rgba($primary-color, 0.001) 50%, rgba($primary-color, 10) 100%);
}

.sidebar__content {
  padding: 0 2rem;
  margin-top: -5rem;

  @include tablet {
    flex-grow: 1;
  }
}

.sidebar__footer {
  display: flex;
  justify-content: space-between;
  padding: 0 2rem;
}
