.subnav {
  position: absolute;
  top: 100%;
  left: 0;

  display: none;

  width: 16.5rem;
  padding: .5rem 0;

  background-color: $dodger-color;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 6px 22px rgba($black-color, .25);

  @include tablet {
    position: static;

    width: 100%;
    margin-left: 1rem;

    background-color: transparent;
    box-shadow: none;
  }
}

.subnav:last-child {
  margin-bottom: 2rem;
}

.subnav__link {
  display: block;
  padding: 0.45rem 1.2rem;

  font-size: 1.2rem;

  color: $white-color;

  transition: background-color .2s ease-in-out;

  &:hover {
    background-color: darken($dodger-color, 10%);
  }

  @include tablet {
    padding-left: 0;
    padding-right: 0;

    font-size: 1.5rem;

    &:hover {
      background: none;
    }
  }
}
