.cabinet {
  display: flex;

  @include mobile {
    flex-direction: column;
  }
}

.cabinet__form {
  width: 34rem;

  @include mobile {
    order: 2;
  }
}

.cabinet__avatar {

  width: 15rem;
  height: 15rem;
  flex-grow: 1;  //оставшееся место занимает avatar
  padding-left: 11rem;

  img {

    @include img-cover;
    margin-bottom: 1.5rem;
  }

  @include mobile {
    order: 1;
    padding-left: 0;
    margin-bottom: 7rem;
  }
}

.cabinet__file {

  font-size: 1.4rem;
  text-transform: lowercase;
  text-decoration: underline;

  color: $dodger-color;
  
  cursor: pointer;

  input {
    display: none;
  }

  &:hover {
    text-decoration: none;
  }
}
