.add-comments {
  margin-top: 3rem;
}

.comments {
  margin-top: 5rem;
}

.comments__item {
  margin-bottom: 3rem;

  >.comments {
    padding-left: 3rem;
    margin-top: 3rem;

    @include mobile {
      padding-left: 1.5rem;
    }
  }
}

.comments__header {
  display: flex;
  margin-bottom: 1.5rem;
}

.comments__avatar {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.comments__author {
  flex-grow: 1;
  margin-left: 1.5rem;

  font-size: 1.2rem;
  line-height: 1.4;
}

.comments__name {
  font-weight: 500;
  color: $white-color;
}

.comments__pabdate {
  color: #757575;
}

.comments__text {
  margin-bottom: 1rem;

  font-size: 1.2rem;
  line-height: 1.3;

  color: $white-color;
}

.comments__reply {
  padding: 0;

  font-size: 1.2rem;
  font-family: inherit;
  line-height: 1.1;
  text-transform: lowercase;

  color: $dodger-color;
  background: none;
  border: none;

  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
}
