html {
  box-sizing: border-box;

  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;

  font-size: 1.4rem;
  line-height: 1.5;

  color: $white-color;
  background-color: $dark-color;

  &.no-scroll {
    overflow: hidden;
  }

  @include tablet {
    &.show-sidebar {
      overflow: hidden;
    }
  }
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  padding: 0;

  border: none;
  background: transparent;

  cursor: pointer;
}

.list-reset {
  margin: 0;
  padding: 0;

  list-style: none;
}

.container {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 5rem;

  @include mobile {
    padding: 0 1.5rem;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
