.profile {
  @include tablet {
    margin-top: -5rem;

    @include tablet {
      position: relative;

      margin-top: 1rem;
      border-bottom: 1px solid #1c1c1c;
    }
  }
}

.profile__avatar {
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto 2rem;
  position: relative;

  border: 2px solid $white-color;
  box-shadow: 0 7px 20px rgba($black-color, .25);
  border-radius: 50%;

  @include tablet {
    position: absolute;
    left: 0;

    width: 6.5rem;
    height: 6.5rem;
    margin-left: 0;
   }
}

.profile__header {
  margin-bottom: 2rem;

  text-align: center;

  @include tablet {
    padding-top: 2rem;
    padding-left: 8rem;
    text-align: left;
  }
}

.profile__name {
  font-size: 1.8rem;

  @include tablet {
    font-size: 1.5rem;
  }
}

.profile__prof {
  margin-bottom: 2rem;

  font-size: 1.4rem;
  font-weight: 300;
  
  color: #dedede;

  @include tablet {
    font-size: 1.2rem;
  }
}

.profile__text {
  padding: 3rem 0;
  margin: 3rem 0;

  font-size: 1.2rem;
  text-align: center;

  border-top: 1px solid #1c1c1c;
  border-bottom: 1px solid #1c1c1c;

  @include tablet {
    display: none;
  }
}
