.related {
  padding: 3rem 0 .5rem;

  border-bottom: 1px solid #1c1c1c;
}

.related__list {
  display: flex;
  flex-wrap: wrap;

  @include mobile {
    flex-direction: column;
  }
}

.related__item {
  width: 50%;
  margin-bottom: 2.5rem;
  padding-right: 2.4rem;

  @include mobile {
    width: 100%;
    margin-bottom: 0;
    padding-right: 0;
  }
}

.related__item-title {
  overflow: hidden;
  margin-bottom: .5rem;

  font-size: 1.4rem;
  font-weight: 500;
  text-overflow: ellipsis;
  white-space: nowrap;

  color: $gold-color;
  //строка с точками...

  a {
    color: inherit;

    &:hover {
      text-decoration: underline;
    }
  }
}

.related__date {
  font-size: 1.2rem;
  color: #828282;
}
