.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  @include tablet {

    .show-sidebar & {
      position: relative;
    }
  }
}

.primary {
  padding: 10rem 0 8rem var(--sidebar-width);

  @include tablet {
    padding: 7rem 0 5rem;
  }
}

.primary__title {
  margin-bottom: 3rem;

  font-size: 2.1rem;

  color: $white-color;

  &-center {
    text-align: center;
  }
}

.page__mask {

  @include tablet {
    display: block;
    width: 100%;
    height: 100%;

    background-color: rgba($black-color, .9);

    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
  }
  // стили применяются только после того как у потомка появится класс .show-sidebar.
}
