.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-index-modal);

  // display: flex;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 1rem;


  background-color: rgba($black-color, .8);

  &.show {
    display: flex;
  }
}

.modal__content {
  position: relative;

  width: 100%;
  max-width: 500px;
  padding: 8rem;

  background-color: $primary-color;
  border-radius: 5px;
  opacity: 0;

  transform: translateY(-50px);
  transition: transform 200ms ease-in-out,
  opacity 200ms ease-in-out;

  &-contact {
    padding-bottom: 2.5rem;
  }

  &-story {
    padding: 0;
    background: none;
  }

  @include tablet {
    padding: 3rem;
    width: auto;
  }
}

.modal__close {
  position: absolute;
  right: 2rem;
  top: 2rem;

  width: 18px;
  height: 18px;
  padding: 0;
  
  background: none;
  border: none;
  cursor: pointer;
  opacity: .3;

  transition: transform .2s linear, opacity .3s linear;

  &:hover {
    transform: rotate(90deg);
    opacity: .6;
  }
}

.modal__footer {
  display: flex;
  justify-content: center;
  margin-top: 4rem;

  font-size: 1.2rem;
  color: #b2b2b2;

  li {
    margin: 0 .5rem;
  }

  a {
    margin: 0 .5rem;

    text-decoration: underline;
    color: #b2b2b2;

    &:hover {
      text-decoration: none;
    }
  }
}

.modal__video {
  display: block;
  margin: 0 auto;
}
